'use client';

import { useState, useEffect, useCallback, useRef } from 'react';
import Image from 'next/image';
import Link from 'next/link';
import Lottie from 'lottie-react';
import redCarAnimation from '../../../public/Red Car.json';
import greenTruckAnimation from '../../../public/Green Trucks.json';

interface Contract {
  id: number;
  phone_number: string;
  lot_number: string;
  full_name: string;
  address: string;
  gatepass: string;
  timestamp: string;
}

export default function AdminDashboard() {
  const [contracts, setContracts] = useState<Contract[]>([]);
  const [loading, setLoading] = useState(true);
  const [selectedSignature, setSelectedSignature] = useState<{
    id: number;
    lotNumber: string;
    fullName: string;
    timestamp: string;
    signatureData: string;
  } | null>(null);
  const [searchTerm, setSearchTerm] = useState('');
  const [searchInput, setSearchInput] = useState('');
  const [currentPage, setCurrentPage] = useState(1);
  const [totalContracts, setTotalContracts] = useState(0);
  const [showDeleteModal, setShowDeleteModal] = useState(false);
  const [deleteData, setDeleteData] = useState<{
    contractId: string;
    lotNumber: string;
    fullName: string;
  } | null>(null);
  const recordsPerPage = 20;

  // Refs to keep current values for SSE callback
  const currentPageRef = useRef(currentPage);
  const searchTermRef = useRef(searchTerm);
  const searchInputRef = useRef(searchInput);

  const loadContracts = useCallback(async (page = 1, search = '') => {
    try {
      const params = new URLSearchParams({
        page: page.toString(),
        limit: recordsPerPage.toString(),
        search: search
      });
      
      const response = await fetch(`/api/contracts?${params}`);
      const data = await response.json();
      
      setContracts(data.contracts || data);
      setTotalContracts(data.total || (data.contracts || data).length);
    } catch (error) {
      console.error('Error loading contracts:', error);
    } finally {
      setLoading(false);
    }
  }, [recordsPerPage]);

  // Update refs when state changes
  useEffect(() => {
    currentPageRef.current = currentPage;
  }, [currentPage]);

  useEffect(() => {
    searchTermRef.current = searchTerm;
  }, [searchTerm]);

  useEffect(() => {
    searchInputRef.current = searchInput;
  }, [searchInput]);

  useEffect(() => {
    loadContracts(currentPage, searchTerm);
  }, [currentPage, searchTerm, loadContracts]);

  useEffect(() => {
    // Setup Server-Sent Events for real-time updates (only once)
    const eventSource = new EventSource('/api/contracts/stream');
    
    eventSource.onmessage = (event) => {
      const data = JSON.parse(event.data);
      if (data.type === 'new_contract') {
        // Reload contracts using current ref values
        loadContracts(currentPageRef.current, searchTermRef.current);
      }
    };

    eventSource.onerror = (error) => {
      console.error('SSE connection error:', error);
    };

    return () => {
      eventSource.close();
    };
  }, [loadContracts]); // Only depends on loadContracts

  const handleSearch = () => {
    // Validar que el número de lote tenga exactamente 8 caracteres si se está buscando
    if (searchInput.trim() !== '' && searchInput.trim().length !== 8) {
      alert('El número de lote debe tener exactamente 8 caracteres');
      return;
    }
    setSearchTerm(searchInput.trim().toUpperCase());
    setCurrentPage(1); // Reset to first page when searching
  };

  const handleClearSearch = () => {
    setSearchInput('');
    setSearchTerm('');
    setCurrentPage(1);
  };


  const totalPages = Math.ceil(totalContracts / recordsPerPage);

  const handlePageChange = (page: number) => {
    setCurrentPage(page);
  };

  const viewSignature = async (contractId: number, lotNumber: string, fullName: string, timestamp: string) => {
    try {
      const response = await fetch(`/api/contracts/${contractId}/signature`);
      const data = await response.json();
      
      setSelectedSignature({
        id: contractId,
        lotNumber,
        fullName,
        timestamp,
        signatureData: data.signatureData
      });
    } catch (error) {
      console.error('Error loading signature:', error);
      alert('Error al cargar la firma');
    }
  };

  const openDeleteModal = (contractId: string, lotNumber: string, fullName: string) => {
    setDeleteData({ contractId, lotNumber, fullName });
    setShowDeleteModal(true);
  };

  const confirmDelete = async () => {
    if (!deleteData) return;
    
    // Cerrar modal inmediatamente
    setShowDeleteModal(false);
    setDeleteData(null);
    
    try {
      const response = await fetch(`/api/contracts/${deleteData.contractId}`, {
        method: 'DELETE',
      });
      
      if (response.ok) {
        loadContracts(currentPage, searchTerm);
      } else {
        throw new Error('Error al eliminar');
      }
    } catch {
      // Silenciar errores en consola
      loadContracts(currentPage, searchTerm);
    }
  };

  const cancelDelete = () => {
    setShowDeleteModal(false);
    setDeleteData(null);
  };

  const formatTimestamp = (timestamp: string) => {
    const date = new Date(timestamp);
    return date.toLocaleString('es-ES', {
      year: 'numeric',
      month: '2-digit',
      day: '2-digit',
      hour: '2-digit',
      minute: '2-digit',
      second: '2-digit'
    });
  };


  return (
    <div className="min-h-screen bg-gray-50">
      <div className="w-full px-6 py-4">
        {/* Header */}
        <div className="bg-black rounded-3xl shadow-lg mb-8 overflow-hidden text-white">
          <div className="px-12 py-8 text-center">
            <h1 className="text-4xl font-bold mb-3">Jorge Minnesota Logistics LLC</h1>
            <p className="text-gray-100 text-xl">Registro de Contratos de Vehículos</p>
          </div>
        </div>

        {/* Dashboard Statistics */}
        <div className="grid grid-cols-1 lg:grid-cols-3 gap-8 mb-8">
          <div className="bg-white rounded-3xl p-8 border-l-4 border-black shadow-sm">
            <div className="flex items-center justify-between">
              <div>
                <div className="text-5xl font-bold text-black mb-2">
                  {contracts.filter(contract => {
                    const contractDate = new Date(contract.timestamp);
                    const today = new Date();
                    return contractDate.toDateString() === today.toDateString();
                  }).length}
                </div>
                <p className="text-gray-600 text-lg font-medium">Contratos Hoy</p>
              </div>
              <div className="w-40 h-40 flex-shrink-0">
                <Lottie 
                  animationData={redCarAnimation} 
                  loop={true} 
                  autoplay={true}
                  style={{ width: '100%', height: '100%' }}
                />
              </div>
            </div>
          </div>

          <div className="bg-white rounded-3xl p-8 border-l-4 border-black shadow-sm">
            <div className="flex items-center justify-between">
              <div>
                <div className="text-5xl font-bold text-black mb-2">
                  {contracts.filter(contract => {
                    const contractDate = new Date(contract.timestamp);
                    const today = new Date();
                    const weekStart = new Date(today);
                    weekStart.setDate(today.getDate() - today.getDay());
                    return contractDate >= weekStart && contractDate <= today;
                  }).length}
                </div>
                <p className="text-gray-600 text-lg font-medium">Esta Semana</p>
              </div>
            </div>
          </div>

          <div className="bg-white rounded-3xl p-8 border-l-4 border-green-500 shadow-sm">
            <div className="flex items-center justify-between">
              <div>
                <div className="text-5xl font-bold text-green-600 mb-2">{totalContracts}</div>
                <p className="text-gray-600 text-lg font-medium">Total Completados</p>
              </div>
              <div className="w-40 h-40 flex-shrink-0">
                <Lottie 
                  animationData={greenTruckAnimation} 
                  loop={true} 
                  autoplay={true}
                  style={{ width: '100%', height: '100%' }}
                />
              </div>
            </div>
          </div>
        </div>

        <div className="space-y-3">
        {/* Lista de Contratos Section */}
        <div className="bg-black text-white p-4 rounded-t-2xl">
          <div className="flex flex-col sm:flex-row items-start sm:items-center justify-between gap-3 mb-4">
            <h2 className="text-xl font-bold">Lista de Contratos</h2>
            <div className="flex flex-col sm:flex-row items-stretch sm:items-center gap-2">
              <Link
                href="/users"
                className="px-4 py-2 bg-white/20 hover:bg-white/30 rounded-lg text-sm font-medium flex items-center gap-2"
              >
                <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 4.354a4 4 0 110 5.292M15 21H3v-1a6 6 0 0112 0v1zm0 0h6v-1a6 6 0 00-9-5.197m13.5-9a2.25 2.25 0 11-4.5 0 2.25 2.25 0 014.5 0z" />
                </svg>
                Usuarios
              </Link>
              <div className="relative">
                <input
                  type="text"
                  placeholder="Buscar"
                  value={searchInput}
                  onChange={(e) => {
                    const value = e.target.value.toUpperCase();
                    if (value.length <= 8) {
                      setSearchInput(value);
                    }
                  }}
                  onKeyPress={(e) => {
                    if (e.key === 'Enter') {
                      handleSearch();
                    }
                  }}
                  className="px-4 py-2 rounded-lg bg-white text-gray-800 placeholder-gray-500 focus:outline-none focus:ring-2 focus:ring-white/50 text-sm w-48"
                  maxLength={8}
                />
              </div>
              <button
                onClick={handleSearch}
                className="px-4 py-2 bg-white/20 hover:bg-white/30 rounded-lg text-sm font-medium flex items-center gap-2"
              >
                <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
                </svg>
                Buscar
              </button>
              <button className="px-4 py-2 bg-white/20 hover:bg-white/30 rounded-lg text-sm font-medium">
                Actualizar
              </button>
            </div>
          </div>
          
          <div className="text-sm opacity-90">
            Mostrando {contracts.length} de {totalContracts} contratos
            {searchTerm && (
              <span> • Filtrado por: &quot;{searchTerm}&quot;</span>
            )}
          </div>
        </div>

        {loading ? (
          <div className="bg-white rounded-xl border border-gray-100 text-center py-16">
            <div className="animate-spin rounded-full h-8 w-8 sm:h-12 sm:w-12 border-b-2 border-black mx-auto mb-4"></div>
            <span className="text-sm sm:text-base text-gray-500">Cargando contratos...</span>
          </div>
        ) : contracts.length === 0 ? (
          <div className="bg-white rounded-xl border border-gray-100 text-center py-16">
            <div className="max-w-md mx-auto">
              <div className="w-16 h-16 mx-auto mb-6 bg-gray-100 rounded-full flex items-center justify-center">
                <svg className="w-8 h-8 text-gray-400" fill="currentColor" viewBox="0 0 24 24">
                  <path d="M5 11l1.5-4.5h11L19 11H5zm1.5 5c-.83 0-1.5-.67-1.5-1.5S5.67 13 6.5 13s1.5.67 1.5 1.5S7.33 16 6.5 16zm11 0c-.83 0-1.5-.67-1.5-1.5s.67-1.5 1.5-1.5 1.5.67 1.5 1.5-.67 1.5-1.5 1.5zM18.92 6.01C18.72 5.42 18.16 5 17.5 5h-11c-.66 0-1.22.42-1.42 1.01L3 12v7c0 .55.45 1 1 1h1c.55 0 1-.45 1-1v-1h12v1c0 .55.45 1 1 1h1c.55 0 1-.45 1-1v-7l-2.08-5.99z"/>
                </svg>
              </div>
              <h3 className="text-lg font-medium text-gray-900 mb-2">No hay contratos registrados</h3>
              <p className="text-gray-500 mb-6">Los contratos aparecerán aquí cuando se registren en el sistema</p>
            </div>
          </div>
        ) : (
          <div className="bg-white rounded-b-2xl shadow-sm overflow-hidden">
            {/* Table Headers */}
            <div className="bg-white border-b border-gray-200">
              <div className="grid grid-cols-12 gap-4 px-6 py-4 text-sm font-semibold text-gray-700">
                <div className="col-span-1"></div>
                <div className="col-span-3">Nombre</div>
                <div className="col-span-2">Lote</div>
                <div className="col-span-2">Gatepass</div>
                <div className="col-span-3">Fecha y Hora</div>
                <div className="col-span-1">Firma</div>
              </div>
            </div>
            
            {/* Table Rows */}
            <div className="divide-y divide-gray-100">
              {contracts.map((contract, index) => (
                <div key={contract.id} className="hover:bg-gray-50 transition-colors">
                  <div className="grid grid-cols-12 gap-4 px-6 py-4 items-center">
                    <div className="col-span-1">
                      <div className="w-8 h-8 bg-gray-100 rounded-full flex items-center justify-center">
                        <svg className="w-4 h-4 text-black" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M20 13V6a2 2 0 00-2-2H6a2 2 0 00-2 2v7m16 0v5a2 2 0 01-2 2H6a2 2 0 01-2-2v-5m16 0H4m16 0l-2-2m0 0l-2 2m2-2v-4M4 13l2-2m0 0l2 2m-2-2v-4" />
                        </svg>
                      </div>
                    </div>
                    <div className="col-span-3 text-sm text-gray-900">
                      {contract.full_name || 'N/A'}
                    </div>
                    <div className="col-span-2">
                      <span className="text-lg font-bold text-black font-mono">
                        {contract.lot_number}
                      </span>
                    </div>
                    <div className="col-span-2 text-sm text-gray-600">
                      <span className="font-mono">
                        {contract.gatepass || 'N/A'}
                      </span>
                    </div>
                    <div className="col-span-3 text-sm text-gray-600">
                      {new Date(contract.timestamp).toLocaleDateString('es-ES', { 
                        weekday: 'long',
                        day: 'numeric',
                        month: 'short', 
                        year: 'numeric'
                      }).replace(/^\w/, (c) => c.toUpperCase())} {new Date(contract.timestamp).toLocaleTimeString('es-ES', { 
                        hour: '2-digit', 
                        minute: '2-digit'
                      })}
                    </div>
                    <div className="col-span-1 flex gap-2">
                      <button
                        onClick={() => viewSignature(
                          contract.id,
                          contract.lot_number,
                          contract.full_name || 'N/A',
                          contract.timestamp
                        )}
                        className="px-3 py-1.5 bg-black text-white text-sm rounded hover:bg-gray-800 transition-all transform hover:scale-105 active:scale-95 cursor-pointer shadow-sm hover:shadow-md"
                      >
                        Ver Firma
                      </button>
                      <button
                        onClick={() => openDeleteModal(
                          contract.id.toString(),
                          contract.lot_number,
                          contract.full_name || 'N/A'
                        )}
                        className="p-1.5 text-black hover:bg-gray-50 rounded transition-colors"
                      >
                        <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16" />
                        </svg>
                      </button>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </div>
        )}

        {/* Pagination */}
        {totalPages > 1 && (
          <div className="mt-6 flex flex-col sm:flex-row justify-center items-center gap-2 sm:gap-4">
            <div className="flex items-center gap-1 sm:gap-2">
              <button
                onClick={() => handlePageChange(currentPage - 1)}
                disabled={currentPage === 1}
                className={`px-3 py-2 rounded-xl text-sm transition-all duration-200 font-medium ${
                  currentPage === 1 
                    ? 'bg-gray-200 text-gray-400 cursor-not-allowed' 
                    : 'bg-black text-white hover:bg-gray-800 hover:shadow-md'
                }`}
              >
                <span className="hidden sm:inline">Anterior</span>
                <span className="sm:hidden">←</span>
              </button>
              
              <div className="flex gap-1 mx-2">
                {Array.from({ length: Math.min(3, totalPages) }, (_, i) => {
                  let pageNum;
                  if (totalPages <= 3) {
                    pageNum = i + 1;
                  } else {
                    const start = Math.max(1, currentPage - 1);
                    const end = Math.min(totalPages, start + 2);
                    pageNum = start + i;
                    if (pageNum > end) return null;
                  }
                  
                  return (
                    <button
                      key={pageNum}
                      onClick={() => handlePageChange(pageNum)}
                      className={`px-3 py-2 rounded-xl text-sm min-w-[36px] transition-all duration-200 font-medium ${
                        currentPage === pageNum
                          ? 'bg-black text-white shadow-md'
                          : 'bg-white text-black border border-gray-200 hover:bg-gray-50 hover:shadow-sm'
                      }`}
                    >
                      {pageNum}
                    </button>
                  );
                })}
              </div>
              
              <button
                onClick={() => handlePageChange(currentPage + 1)}
                disabled={currentPage === totalPages}
                className={`px-3 py-2 rounded-xl text-sm transition-all duration-200 font-medium ${
                  currentPage === totalPages 
                    ? 'bg-gray-200 text-gray-400 cursor-not-allowed' 
                    : 'bg-black text-white hover:bg-gray-800 hover:shadow-md'
                }`}
              >
                <span className="hidden sm:inline">Siguiente</span>
                <span className="sm:hidden">→</span>
              </button>
            </div>
            
            <div className="text-sm text-gray-500 mt-2 sm:mt-0">
              Página {currentPage} de {totalPages}
            </div>
          </div>
        )}
        </div>
      </div>

      {/* Signature Modal */}
      {selectedSignature && (
        <div className="fixed inset-0 bg-black/50 backdrop-blur-sm flex items-center justify-center p-2 sm:p-4 z-50">
          <div className="bg-white rounded-2xl shadow-2xl border border-gray-100 p-4 sm:p-6 lg:p-8 max-w-2xl w-full mx-2 sm:mx-4 max-h-[95vh] sm:max-h-[90vh] overflow-y-auto">
            <div className="flex justify-between items-center mb-4 sm:mb-6">
              <h3 className="text-lg sm:text-xl lg:text-2xl font-bold text-gray-800">Firma del Contrato</h3>
              <button
                onClick={() => setSelectedSignature(null)}
                className="w-8 h-8 sm:w-10 sm:h-10 bg-gray-100 hover:bg-gray-200 rounded-full flex items-center justify-center text-gray-500 hover:text-gray-700 transition-all transform hover:scale-110 active:scale-95 cursor-pointer shadow-sm hover:shadow-md"
              >
                <svg className="w-4 h-4 sm:w-5 sm:h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                </svg>
              </button>
            </div>
            
            <div className="space-y-2 sm:space-y-3 lg:space-y-4 mb-4 sm:mb-6">
              <p className="text-sm sm:text-base break-words"><strong>Nombre:</strong> <span className="text-black">{selectedSignature.fullName}</span></p>
              <p className="text-sm sm:text-base break-all"><strong>Lote:</strong> <span className="font-mono text-black">{selectedSignature.lotNumber}</span></p>
              <p className="text-sm sm:text-base"><strong>Fecha:</strong> {formatTimestamp(selectedSignature.timestamp)}</p>
            </div>
            
            <div className="border border-gray-200 rounded-xl p-3 sm:p-4 bg-gray-50/30">
              <p className="text-xs sm:text-sm text-gray-600 mb-2 font-medium">Firma Digital:</p>
              <div className="overflow-hidden rounded-xl bg-white border border-gray-100">
                <Image
                  src={selectedSignature.signatureData}
                  alt="Firma"
                  className="w-full max-w-full rounded-xl"
                  style={{ maxHeight: '300px', objectFit: 'contain' }}
                  width={500}
                  height={300}
                />
              </div>
            </div>
          </div>
        </div>
      )}

      {/* Delete Confirmation Modal */}
      {showDeleteModal && deleteData && (
        <div className="fixed inset-0 bg-black/50 backdrop-blur-sm flex items-center justify-center p-4 z-50">
          <div className="bg-white rounded-2xl shadow-2xl border border-gray-100 max-w-md w-full mx-4 p-6 sm:p-8 text-center">
            <div className="mb-4 sm:mb-6">
              <div className="mx-auto w-12 h-12 sm:w-16 sm:h-16 bg-red-100 rounded-full flex items-center justify-center mb-3 sm:mb-4">
                <svg className="w-6 h-6 sm:w-8 sm:h-8 text-red-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16" />
                </svg>
              </div>
              <h2 className="text-xl sm:text-2xl font-bold text-gray-800 mb-2">
                Confirmar Eliminación
              </h2>
              <p className="text-gray-600 mb-3 sm:mb-4 text-sm sm:text-base leading-relaxed">
                ¿Está seguro de que desea eliminar el vehículo del lote{' '}
                <span className="font-mono font-bold text-black">{deleteData.lotNumber}</span>{' '}
                de <span className="font-semibold text-black">{deleteData.fullName}</span>?
              </p>
              <p className="text-xs sm:text-sm text-gray-500">
                Esta acción no se puede deshacer.
              </p>
            </div>
            
            <div className="flex flex-col sm:flex-row gap-3 sm:gap-4">
              <button
                onClick={cancelDelete}
                className="flex-1 py-2.5 sm:py-3 bg-gray-200 text-gray-700 font-semibold rounded-xl hover:bg-gray-300 hover:shadow-md transform hover:scale-105 active:scale-95 transition-all duration-200 text-sm sm:text-base cursor-pointer"
              >
                Cancelar
              </button>
              <button
                onClick={confirmDelete}
                className="flex-1 py-2.5 sm:py-3 bg-black text-white font-semibold rounded-xl hover:bg-gray-800 hover:shadow-lg transform hover:scale-105 active:scale-95 transition-all duration-200 text-sm sm:text-base cursor-pointer"
              >
                Eliminar Vehículo
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}